body {
    margin: 0px;
    padding: 0px;
}


.game {
    display: flex;
    flex-direction: column;
    font-family: Lato;
    border: 2px solid #c5c6c7;
    height: 700px;
    width: 600px;
    margin-top: 40px;


    &__board {
        box-sizing: border-box;
        /* height: 400px; */
        /* width: 400px; */
        background-color: #c5c6c7;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        grid-template-rows: 1fr 1fr 1fr;
        grid-column-gap: 5px;
        grid-row-gap: 5px;
        pointer-events: auto;
        margin: 10px auto;




        &__square {
            background-color: #ffffff;
            color: #2a1b3d;
            padding: 10px;
            font-size: 20px;
            cursor: pointer;
            width: 100px;
            height: 100px;
            font-size: 60px;
            /* margin: 0px auto; */
            text-align: center;
            box-sizing: border-box;
        }



    }

    &__title {
        font-size: 40px;
        color: black;
        /* font-weight: bold; */
        margin: 0px auto 20px;
        background: #c5c6c7;
        text-align: center;
        padding: 20px 80px;
    }


    &__reset {
        width: 100px;
        margin: 80px auto 20px;
        background: #c5c6c7;
        color: black;
        height: 60px;
        font-size: 16px;
        width: 200px;
    }

    &__turn {
        font-size: 30px;

        color: #c5c6c7;
        margin: 10px auto 20px;
    }
}