@import url(//maxcdn.bootstrapcdn.com/font-awesome/4.1.0/css/font-awesome.min.css);
body{margin:0px;padding:0px}.game{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;font-family:Lato;border:2px solid #c5c6c7;height:700px;width:600px;margin-top:40px}.game__board{box-sizing:border-box;background-color:#c5c6c7;display:grid;grid-template-columns:1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr;grid-column-gap:5px;grid-row-gap:5px;pointer-events:auto;margin:10px auto}.game__board__square{background-color:#ffffff;color:#2a1b3d;padding:10px;font-size:20px;cursor:pointer;width:100px;height:100px;font-size:60px;text-align:center;box-sizing:border-box}.game__title{font-size:40px;color:black;margin:0px auto 20px;background:#c5c6c7;text-align:center;padding:20px 80px}.game__reset{width:100px;margin:80px auto 20px;background:#c5c6c7;color:black;height:60px;font-size:16px;width:200px}.game__turn{font-size:30px;color:#c5c6c7;margin:10px auto 20px}

body {

    box-sizing: border-box;
    font-family: Lato;
    margin: 0px;
    padding: 0px;

}

a {
    text-decoration: none;
    color: black;
}

.autocomplete {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
}

.autocomplete__results {
    max-height:200px;
    margin: 0 auto;
    overflow: scroll;
    overflow-x: hidden;
}

.autocomplete__header {
    margin: 0px;
    background-color: #2a1b3d;
    /* width: 100vw; */
    height: 100px;
    font-size: 40px;
    text-align: center;
    padding: 30px;
    border: 1px solid #c5c6c7;
    box-sizing: border-box;
    color:#c5c6c7;


}


.autocomplete__input {

    height: 50px;
    width: 800px;
    border: 1px solid black;
    text-decoration: none;
    font-size: 18px;


}

.autocomplete__result {
    border: 1px solid #000000;
    width: 800px;
    margin: 0 auto;
    padding: 0px 0px 0px;
    padding: 1;
}

.autocomplete__result__output {
    box-sizing: border-box;
    /* margin: 0px auto; */
    padding: 15px;
    /* width: 800px; */
    height: 50px;
    /* border: 1px solid #800000; */
    background-color: #ffffff;
    text-align: center;
    font-size: 18px;
}

.autocomplete__result__output:hover {

    color:#2a1b3d;
    background-color: #c5c6c7


    


}

.autocomplete__search {
    position: relative;
    margin: 0 auto;
}

.autocomplete__search input {
    text-indent: 30px;
}

.autocomplete__search .fa-search {
    position: absolute;
    top: 20px;
    left: 10px;
    font-size: 15px;
}

.projects{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:space-evenly;justify-content:space-evenly;height:100vh;background-color:#2a1b3d}.projects .autocompletes{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-evenly;justify-content:space-evenly}::-webkit-scrollbar{width:12px}::-webkit-scrollbar-track{-webkit-box-shadow:inset 0 0 6px rgba(0,0,0,0.3);border-radius:8px}::-webkit-scrollbar-thumb{border-radius:8px;background:#c5c6c7;-webkit-box-shadow:inset 0 0 6px rgba(0,0,0,0.5)}::-webkit-scrollbar-thumb:window-inactive{background:white}

